.flat {
  box-sizing: border-box;
  flex-basis: calc(50% - 20px);
  margin: 10px;
  cursor: pointer;
  border: 5px solid transparent;
  transition: all .2s;
}

.flat.selected {
  border: 5px solid #ff8d8d;
}

.flat:hover {
  opacity: .9;
}

.flat-picture {
  height: 200px;
  background-size: cover;
  background-position: center;
}

.flat-title {
  margin-top: 5px;
  padding: 5px;
  font-weight: bold;
}